export default {
  name: 'Home',
  // metaInfo: {
  //   title: `${locale.getMessage('meta.default_title')}`,
  //   titleTemplate: `%s`,
  //   meta: [{charset: 'utf-8'}, {
  //     name: 'description',
  //     content: `${locale.getMessage('meta.home_description')}`
  //   }, {name: 'keyword', content: `${locale.getMessage('meta.home_keyword')}`}],
  //   link: [
  //     {rel: 'canonical', href: `https://www.krikya11.com/home`}
  //   ]
  // },
  components: {
    Hooper,
    Slide,
    HooperPagination,
    RecentWinner,
    AppHomeBanner,
    AppPlayWithUs,
    RecentDeposit,
    RecentWithdrawal,
    // AppSportCasinoHolder,
    // NavSlot,
    // NavCasino,
    // NavAll,
    // NavSports,
    AppVideo,
    AppHomeHighlight,
