    AppReferral,
    AppReferralHomeBanner
  },
  data: () => ({
    scrollPosition: 0, // Current scroll position
    scrollSpeed: 1, // Scroll speed in pixels per frame
    isUserScrolling: false, // Flag to track if the user is scrolling
    isAutoScrolling: false, // Flag to track if auto-scroll is active
    maxScroll: 0, // Maximum scroll position (right end)
    mediaUrl: SHARED.MEDIA_SERVER_URL,
    selectedButton: 'rng',
    GainCoins: [
      { id: 1, name: 'Deposit & Free Coins', shortName: 'deposit', btn: 'Deposit', route: 'funds' },
      { id: 2, name: 'Daily Check In', shortName: 'daily', btn: 'Check-In', route: 'rewards' },
      { id: 3, name: 'Grab Coin', shortName: 'grabCoins', btn: 'Grab Coins', route: 'grab-coin' }
    ],
    SpentCoins: [
      { id: 1, name: 'Reward Store', shortName: 'rewardStore', btn: 'Redeem', route: 'rewardStore' },
      { id: 2, name: 'Krikya Wheel', shortName: 'spin', btn: 'Spin Now', route: 'wheel-of-fortune' }
    ],
    showCoin: true,
    selectionType: 'all',
    storageAnnouncement: null,
    currentLanguage: uiHelper.getLanguage(),
    filteredAnnouncement: [],
    isAndroid: uiHelper.isAndroid(),
    platform: uiHelper.getPlatform()
  }),
  computed: {
    filteredWinningRecord() {
      let records = this.$store.state.game.homePageWinningRecord.data || []

      // Filter records based on the selected button and condition for `gt`
      switch (this.selectedButton) {
        case 'rng':
          return records.filter(record => record.gt === 1) // Filter by gt = 1
        case 'ld':
          return records.filter(record => record.gt === 3) // Filter by gt = 2
        case 'sb':
          return records.filter(record => record.gt === 4 || record.gt === 10) // Filter by gt = 3
        case 'fishing':
          return records.filter(record => record.gt === 2) // Filter by gt = 4
        case 'crash':
          return records.filter(record => record.gt === 11) // Filter by gt = 5
        case 'cricket':
          return records.filter(record => record.gt === 10) // Filter by gt = 6
        case 'table':
          return records.filter(record => record.gt === 9) // Filter by gt = 7
        default:
          return records // You can return a default case if no conditions are met
      }
    },
    winningRecord() {
      return this.$store.state.game.homePageWinningRecord.data || {}
    },
    grabCoin() {
      let d = this.$store.state.bonus.grabCoinSettings
      return d
    },
    wof() {
      return this.$store.state.wheel.wheel
    },
    isLoggedIn() {
      return this.$store.state.member.isLoggedIn
    },
    announcementList() {
      return this.$store.state.announcement.announcements
    },
    memberInfo() {
      return this.$store.state.member.info
    },
    memberWallet() {
      return this.$store.state.member.walletBalance
    }
  },
  watch: {
    winningRecord() {
      if (this.$store.state.game.homePageWinningRecord === null) {
        this.getHomePageWinningRecord()
      }
      return this.$store.state.game.homePageWinningRecord.data || {}
    },
    announcementList() {
      this.storageAnnouncement = this.$store.state.announcement.announcements
      this.populateAnnouncement(this.storageAnnouncement)
    }
  },
  mounted() {
    const ae_parms_kv = {
      event_type: 'home_page', // Event type
      country_code: 'BD', // Country code
      lang_code: uiHelper.getLanguage(), // Language code
      depth: 0
    }
    window.ae_parms_kv = ae_parms_kv
    const el_adv_id = '71880509ee082f8f44596eedf687aac0'
    const scr = document.createElement('script')
    const host = '//d313lzv9559yp9.cloudfront.net'
    scr.type = 'text/javascript'
    scr.async = true
    scr.src = `${host}/${el_adv_id}.js`
    const s = document.getElementsByTagName('script')[0]
    s.parentNode.insertBefore(scr, s)
    this.startAutoScroll()
  },
  created() {
    this.checkShouldHideCoin()
    this.getGrabCoinSetting()
    this.getWOF()
    this.checkIncomingAction()
    this.getAnnouncement()
    this.getMemberDetail()
    this.metaTag()
    this.getHomePageWinningRecord()
  },
  beforeRouteLeave(to, from, next) {
    uiHelper.setMetaTag('og:title', `${locale.getMessage('meta.home_og_title')}`)
    uiHelper.setMetaTag('og:description', `${locale.getMessage('meta.home_og_description')}`)
    next()
  },
  methods: {
    toggleAutoScroll() {
      if (this.isAutoScrolling) {
        // Stop auto-scrolling
        this.isAutoScrolling = false
      } else {
        // Start auto-scrolling from the current position
        this.isUserScrolling = false // Make sure manual scrolling doesn't interfere
        this.isAutoScrolling = true // Start the auto-scrolling
        this.scrollAnimation()
      }
    },
    toggleAutoScrollTrue() {
      // Start auto-scrolling from the current position
      this.isUserScrolling = false // Make sure manual scrolling doesn't interfere
      this.isAutoScrolling = true // Start the auto-scrolling
      this.scrollAnimation()
    },
    toggleAutoScrollFalse() {
      this.isAutoScrolling = false
    },
    handleScroll() {
      const scrollContainer = this.$refs.scrollContainer
      // If the user manually scrolls, mark it as manual scroll
      if (!this.isAutoScrolling) {
        this.isUserScrolling = true
      }
      this.scrollPosition = scrollContainer.scrollLeft
      // Check if the user manually scrolled to the end
      if (scrollContainer.scrollLeft + scrollContainer.clientWidth >= scrollContainer.scrollWidth) {
        this.resetScroll() // Reset the scroll position if the user scrolls to the end
      }
    },
    startAutoScroll() {
      // Initialize auto-scroll
      const scrollContainer = this.$refs.scrollContainer
      this.maxScroll = scrollContainer.scrollWidth - scrollContainer.clientWidth
      this.scrollPosition = scrollContainer.scrollLeft || 0 // Start from the current position
      this.isUserScrolling = false // Ensure that manual scrolling doesn't interfere
      this.isAutoScrolling = true // Mark that we are auto-scrolling

      // Start the animation loop
      this.scrollAnimation()
    },
    scrollAnimation() {
      // Prevent animation if it's stopped
      if (!this.isAutoScrolling) return
      const scrollContainer = this.$refs.scrollContainer
      // Move the scroll position by scrollSpeed each frame
      this.scrollPosition += this.scrollSpeed
      // Apply the new scroll position
      scrollContainer.scrollLeft = this.scrollPosition
      // If we've reached the end of the scroll, reset and start again
      if (this.scrollPosition >= this.maxScroll) {
        this.resetScroll()
        return
      }
      // Continue the animation
      requestAnimationFrame(this.scrollAnimation)
    },
    resetScroll() {
      // Reset the scroll position when the animation completes or user reaches the end
      const scrollContainer = this.$refs.scrollContainer

      // Stop auto-scrolling if it's active
      this.isAutoScrolling = false

      // If the user was scrolling manually, prevent reset during manual scroll
      if (this.isUserScrolling) {
        this.isUserScrolling = false
        return
      }
      // Reset scroll to the beginning
      scrollContainer.scrollLeft = 0
      // Restart auto-scroll after a small delay
      setTimeout(() => {
        this.startAutoScroll()
      }, 20) // Delay before restarting the animation
    },
    selectButton(button) {
      this.selectedButton = button
      this.resetScroll()
    },
    semiTransfer(gameInfo) {
      let obj = {
        to_wallet: gameInfo.provider
      }
      return this.$store.dispatch(`${SEMI_TRANSFER}`, { obj })
    },
    directLaunchSpecificGame(record, isDemo) {
      this.toggleAutoScrollFalse()
      if (!this.isLoggedIn) {
        this.openLoginDialog()
      } else {
        let game_type = 'rng'
        switch (record.gt) {
          case 1:
            game_type = 'rng'
            break
          // Add other cases as needed
          case 2:
            game_type = 'fishing' // For example, another game type
            break
          case 3:
            game_type = 'ld' // For example, another game type
            break
          case 4:
            game_type = 'sb' // For example, another game type
            break
          case 9:
            game_type = 'table' // For example, another game type
            break
          case 10:
            game_type = 'cricket' // For example, another game type
            break
          case 11:
            game_type = 'crash' // For example, another game type
            break
          default:
            game_type = 'rng' // Default to 'rng' if no match
            break
        }
        const game = {
          game_code: record.gc,
          game_id: 0,
          game_type: game_type,
          provider: record.pv.toLowerCase()
        }
        let routeData = this.$router.resolve({
          name: ROUTE_NAME.LAUNCH_GAME_LOBBY,
          params: {
            provider: record.pv.toLowerCase(),
            type: game_type,
            lang: this.$route.params.lang
          },
          query: {
            id: 0,
            code: record.gc,
            demo: isDemo ? 1 : 0
          }
        })
        try {
          this.$ga.event('Game', `${isDemo ? 'Demo' : 'Real'} Play`, `${game.provider} - Slot - ${record.gn}`, 0)
        } catch (err) {
          console.log(err)
        }
        if (record.gt == 4 || record.gt == 10) {
          // await uiHelper.openPopup(routeData.href, 'Game Play Lobby', 1440, 810)
          window.open(routeData.href)
          this.semiTransfer(game)
        } else {
          window.open(routeData.href)
          this.semiTransfer(game).then(() => {})
        }
        // window.open(routeData.href, '_blank')
        // this.semiTransfer(game).then(() => {})
      }
    },
    truncatedGameName(gameName) {
      const words = gameName
        .split(' ')
        .slice(0, 3)
        .join(' ')
      if (gameName.split(' ').length > 2) {
        return words + '...'
      } else {
        return words
      }
    },
    getHomePageWinningRecord() {
      let data = {
        currency: uiHelper.getCurrency(),
        size: 100
      }
      this.$store.dispatch(`${HOMEPAGE_WINNING_RECORD}`, { data })
    },
    handleButtonClick() {
      // Your button click logic here
    },
    downloadAPK() {
      uiHelper.downloadAPK()
    },
    metaTag() {
      const currentUrl = window.location.href
      uiHelper.setCanonical(currentUrl)
      uiHelper.setMetaTag('url', currentUrl)
      uiHelper.setMetaTag('og:url', currentUrl)
      uiHelper.setMetaTag('og:type', 'website')
      uiHelper.setMetaTag('og:site_name', 'Krikya')
      uiHelper.setMetaTag('og:image', `${currentUrl}/static/image/home/logo.webp`)
      uiHelper.setMetaTag('og:title', `${locale.getMessage('meta.home_og_title')}`)
      uiHelper.setMetaTag('og:description', `${locale.getMessage('meta.home_og_description')}`)
      uiHelper.setMetaTag('description', `${locale.getMessage('meta.home_description')}`)
      uiHelper.setMetaTag('keywords', `${locale.getMessage('meta.home_keyword')}`)
      // uiHelper.setCanonical("https://www.krikya11.com/home/");
      uiHelper.setTitle(`${locale.getMessage('meta.home_title')}`)

      const domain_all = window.location.hostname.toLowerCase()

      // Update the og:title content based on the domain
      if (domain_all.includes('krikya.club')) {
        uiHelper.setTitle(`${locale.getMessage('meta.krikya_club_title')}`)
        uiHelper.setMetaTag('description', `${locale.getMessage('meta.krikya_club_description')}`)
      } else if (domain_all.includes('krikya11.com')) {
        if (this.$route.params.lang) {
          uiHelper.setTitle(`${locale.getMessage('meta.krikya_11_title_lang')}`)
          uiHelper.setMetaTag('description', `${locale.getMessage('meta.krikya_11_description_lang')}`)
        } else {
          uiHelper.setTitle(`${locale.getMessage('meta.krikya_11_title')}`)
          uiHelper.setMetaTag('description', `${locale.getMessage('meta.krikya_11_description')}`)
        }
      } else {
        if (this.$route.params.lang) {
          uiHelper.setTitle(`${locale.getMessage('meta.home_lang_title')}`)
          uiHelper.setMetaTag('description', `${locale.getMessage('meta.home_lang_description')}`)
        } else {
          uiHelper.setTitle(`${locale.getMessage('meta.home_title')}`)
          uiHelper.setMetaTag('description', `${locale.getMessage('meta.home_description')}`)
        }
      }
      const domainWithProtocol = currentUrl.split('/')[2]
      const domain = domainWithProtocol.replace(/(^\w+:|^)\/\//, '')
      const Links = `https://${domain}`
      const Linksen = `https://${domain}/en-BD`
      const Linksbn = `https://${domain}/bn-BD`
      uiHelper.setCanonical(currentUrl)
      uiHelper.setHreflangLinks(Links)
      uiHelper.setHreflangLinksen(Linksen)
      uiHelper.setHreflangLinksbd(Linksbn)
    },
    getGrabCoinSetting() {
      let filter = {
        currency: uiHelper.getCurrency()
      }
      this.$store.dispatch(`${GRAB_COIN_SETTING}`, { filter })
    },
    checkShouldHideCoin() {
      if (typeof uiHelper.getCookie(SESSION.HIDE_COIN) != 'undefined' && uiHelper.getCookie(SESSION.HIDE_COIN) == 1) {
        this.showCoin = false
      } else {
        this.showCoin = true
      }
    },
    dontShowMeAgain() {
      this.showCoin = false
      uiHelper.setCookie(SESSION.HIDE_COIN, 1, 0.5)
    },
    navigateToCoinPage() {
      this.$router.push({
        name: ROUTE_NAME.GRAB_COIN,
        params: {
          type: 'coin',
          lang: this.$route.params.lang
        }
      })
    },
    navigateToCardManiaPage() {
      this.$router.push({
        name: ROUTE_NAME.CARD_MANIA,
        params: {
          lang: this.$route.params.lang
        }
      })
    },
    navigateWOFPage() {
      if (uiHelper.getPlatform() == DevicePlatform.DESKTOP) {
        this.$router.push({
          name: ROUTE_NAME.WOF,
          lang: this.$route.params.lang
        })
      } else {
        this.$router.push({
          name: ROUTE_NAME.MWOF,
          lang: this.$route.params.lang
        })
      }
    },
    getWOF() {
      let currency = uiHelper.getCurrency()
      let filter = {
        currency: currency
      }
      this.$store.dispatch(`${WHEEL_MODULE}`, { filter })
    },
    getMemberDetail() {
      if (this.isLoggedIn) this.$store.dispatch(`${MEMBER_DETAIL}`)
    },
    goToAffilateProfilePage() {
      this.$router.push({
        name: ROUTE_NAME.AFFILIATE_LINK,
        lang: this.$route.params.lang
      })
    },
    goToAffilateHomePage() {
      this.$router.push({
        name: ROUTE_NAME.AFFILIATE_HOME,
        lang: this.$route.params.lang
      })
    },
    goToDepositPage() {
      if (uiHelper.getPlatform() == DevicePlatform.MOBILE) {
        let routeData = this.$router.resolve({
          name: ROUTE_NAME.DEPOSIT,
          params: {
            lang: this.$route.params
          }
        })
        window.open(routeData.href)
      } else {
        this.$router.push({
          name: ROUTE_NAME.DEPOSIT,
          params: {
            lang: this.$route.params
          }
        })
      }
    },
    getAnnouncement() {
      if (this.storageAnnouncement == null) {
        let obj = {
          site: uiHelper.getPlatform(),
          type: AnnouncementType.ALL,
          page: '',
          itemsPerPage: ''
        }
        this.$store.dispatch(`${ANNOUNCEMENT_LIST}`, { obj })
      } else {
        this.populateAnnouncement(this.storageAnnouncement)
      }
    },
    populateAnnouncement(announcementData) {
      let p = []
      announcementData.forEach(announcement => {
        p.push({
          id: announcement.id,
          date: announcement.announcementDate,
          title:
            announcement.titleLocale.find(x => x.language == this.currentLanguage) != null
              ? announcement.titleLocale.find(x => x.language == this.currentLanguage).title
              : announcement.titleLocale.find(x => x.language == 'en') != null
              ? announcement.titleLocale.find(x => x.language == 'en').title
              : announcement.titleLocale[0].title,
          message:
            announcement.messageLocale.find(x => x.language == this.currentLanguage) != null
              ? announcement.messageLocale.find(x => x.language == this.currentLanguage).message
              : announcement.messageLocale.find(x => x.language == 'en') != null
              ? announcement.messageLocale.find(x => x.language == 'en').message
              : announcement.messageLocale[0].message
        })
      })
      this.filteredAnnouncement = p
    },
    checkIncomingAction() {
      let action = this.$route.params.action

      if (action == SHARED.RESET_PASSWORD) {
        let memberCode = this.$route.params.memberCode
        let contact = this.$route.params.contact
        if (memberCode != null && memberCode != undefined && contact != null && contact != undefined) this.$parent.openResetPasswordDialog(memberCode, contact)
      } else if (action == SHARED.INFO_VERIFICATION) {
        let memberCode = this.$route.params.memberCode
        let otp = this.$route.params.otp
        let type = this.$route.params.type
        let contact = this.$route.params.contact
        if (memberCode != null && memberCode != undefined && otp != null && otp != undefined && type != null && type != undefined)
          this.$parent.memberContactVerification(memberCode, otp, type, contact)
      } else if (action == SHARED.REGISTER) {
        this.openRegisterDialog()
      }
    },
    openRegisterDialog() {
      this.$parent.openRegisterDialog()
    },
    openLoginDialog() {
      this.$parent.openLoginDialog()
    }
  }
}
</script>
